// Import Susy
@import "../../bower_components/susy/sass/_susy.scss";
// Import reset.scss
@import "../../bower_components/reset-css/_reset.scss";
// Import bourbon
@import "../../bower_components/bourbon/app/assets/stylesheets/_bourbon.scss";
// My variables
@import "inc/_site-variables.scss";

$gutter-width: 10px;
$grid-padding: 10px;

// Setting up the Magic Grid
$gutter-width: 10px; // with 1em gutters
$grid-padding: 10px; // and 1em padding on the grid container

$susy: (
  flow: ltr,
  columns: 12,
  container: 1200px,
  global-box-sizing: border-box,
  // debug: (image: show),
  math: fluid,
  output: isolate,
  gutter-position: inside,
);

.row {
	@include container();
	margin-left: -$gutter-width/2;
	margin-right: -$gutter-width/2;
}

// ++==++
//  ,   
$g-yellow: #ffcd43;
$g-dark-blue: #1b1a1b;
$g-light-blue: #1092dd;
$g-gray: #ddd;
$g-dark-gray: #777;
$g-font-light: 300;
$g-font-regular: 400;
$g-font-bold: 500;

//  
$menuWidth: 210px;
$subMenuPosition: $menuWidth - 50px;
// ++==++

// 
h1 {
	color: $g-light-blue;
	font-size: 48px;
	line-height: 36px;
	font-weight: $g-font-light;
	margin-bottom: 40px;
}
h2 {
	color: $g-dark-blue;
	font-size: 30px;
	line-height: 36px;
	font-weight: $g-font-light;
	margin-bottom: 30px;
}
h3 {
	color: $g-dark-blue;
	font-size: 36px;
	line-height: 36px;
	font-weight: $g-font-regular;
	margin-bottom: 30px;	
}
h4 {
	color: $g-dark-blue;
	font-size: 20px;
	line-height: 24px;
	font-weight: $g-font-regular;
	margin-bottom: 20px;	
}


.wrapper-block {
	width: $mid-wrap;
	margin-left: 270px;
	padding-left: 15px;
	padding-right: 15px;
}
* {
	box-sizing: border-box;
}
// ++==++

.wrapper {
	position: relative;
}

body {
	font-family: 'Roboto', Arial, sans-serif;
	font-size: 16px;
	line-height: 24px;
	color: $g-dark-blue;
	background: url(../img/main-bg.jpg);
}
html {
	&.menu-open {
		overflow: hidden;
		.shadow-box {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 3;
			background: rgba(0,0,0,.6);
			width: 100%;
			height: 100%;
		}
	}
	&.popup-open {
		overflow: hidden;
	}
	
}
a {
	@include transition( all .2s ease-in-out );
}

//       
.wrapper {
    min-height: 100%;
    padding-bottom: 40px;
    margin-bottom: -188px;
}

.wrapper:after {
    content: "";
    display: block
}

.footer-site,.wrapper:after {
    height: 188px;
}
// ++==++

//    
.scrollable {
	padding-right: 1.563em;
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	overflow-y: auto;
	box-sizing: content-box;
}
//    
@mixin close-btn($right: 0px){
	.close {
		position: absolute;
		top: 0;
		right: $right;
		width: 50px;
		height: 50px;
		background: $g-dark-blue;
		text-align: center;
		line-height: 50px;
		cursor: pointer;

		&:before {
			content: "\e646";
			font-family: "themify";
			color: #fff;
			font-size: 24px;
		}
	}
}
@mixin color-text($color: #fff, $size: 16px) {
	text-decoration: none;
	color: $color;
	font-size: $size;
}
@mixin valign($class-block) {
	.#{$class-block} {
		display: inline-block;
		&:after {
			content: "";
			display: inline-block;
			width: 0;
			height: 100%;
		}
	}
}
//  
@mixin link-decor($color: $g-light-blue) {
	color: $color;
	display: inline-block;
	border-bottom: 1px dashed $color;
	text-decoration: none;
}
//   
@mixin pseudo-elem() {
	content: "";
	position: absolute;
	display: block;
}

//        
@mixin menu-padding() {
	padding-left: 30px;
}
//    
@mixin link-inline($class-block, $class-link) {
	.#{$class-block} {
		display: inline-block;
		
		&:before {
			content: "|";
			color: #333;
		}
		&:first-child {
			.#{$class-link} {
				&:first-child {
					padding-left: 0;
					padding-right: 10px;
				}
			}
			&:before {
				content: none;
			}
		}
		.#{$class-link} {
			padding-left: 10px;
		}
	}
}
// ++==++

//  
@mixin personal-btn($color: #ffcd43) {
	background: $color;
	width: 50px;
	height: 50px;
	overflow: hidden;
	box-shadow: rgba(0,0,0,.4) 0 1px 2px;
}

// ++==++

//  
.main-nav {
	position: fixed;
	z-index: 6;
	left: 0;
	top: 0;
	background: $g-dark-blue;
	width: $menuWidth;
	height: 100%;
	min-height: 100%;
	padding-top: 50px;
	border: 1px solid #333;
	border-right: none;
	overflow: hidden;

	
	.main-nav__logo {
		@include menu-padding();
	}

	.scrollable {
		height: 100%;
		min-height: 100%;

		.main-nav__menu {
			width: $menuWidth;
			@include menu-padding();
			margin-top: 55px;
			padding-bottom: 35px;
			border-bottom: 1px solid #333;
				
			.main-nav__link {
				@include color-text();
				line-height: 40px;
				display: block;
				position: relative;
				
				&:after {
					content: "";
					position: absolute;
					top: 50%;
					right: -20px;
					@include transition(all ease-in-out .3s .4s);
					opacity: 0;
					filter:Alpha(Opacity=0);
				}

				&.active {
					color: $g-light-blue;
					&:after {
						opacity: 1;
						filter:Alpha(Opacity=100);
						right: 0;
						margin-top: -10px;
						@include transition(all ease-in-out .3s .4s);
						@include triangle(20px, $g-gray, left);
						
					}
				}
				&:hover {
					color: $g-light-blue;
				}
			}
		}
	}
	// ++==++

	.main-nav__personal {
		@include menu-padding();
		margin-top: 30px;

		.main-nav__personal__head {
			@include color-text();
		}
		
		@include link-inline($class-block: main-nav__personal__b, $class-link: main-nav__personal__link);
		.main-nav__personal__link {
			@include color-text($color: $g-light-blue, $size: 14px);
		}
	}
}

// 
.main-nav__under-level {
	$menu-padding: 20px;
	position: fixed;
	z-index: 5;
	top: 0;
	left: 0;
	@include transform(translateX(0px));
	width: $menuWidth + 50px;
	height: 100%;
	min-height: 100%;

	.under-level__children {
		$submenuWidth: $menuWidth + 40px;
		position: absolute;
		top: 0;
		left: 0;
		width: $submenuWidth;
		opacity: 0;
		visibility: hidden;
		filter:Alpha(Opacity=0);
		background: $g-gray;
		height: 100%;
		min-height: 100%;
		overflow: hidden;
		@include transition(all ease-in-out .4s);

		//     
		.under-level__ul {
			width: $submenuWidth;
			padding-top: 100px;
		}

		&.open {
			visibility: visible;
			opacity: 1;
			filter:Alpha(Opacity=100);
			@include transform(translateX($menuWidth));
		}

		//     js
		@include close-btn();
	}

	//   
	.under-level__ul {
		.under-level__list {
			border-bottom: 1px solid #bbb;

			.under-level__product {
				color: $g-dark-gray;
				line-height: 24px;
				padding: 10px 0;
				font-size: 18px;
				font-weight: $g-font-bold;
				position: relative;
				cursor: pointer;
				padding-left: $menu-padding;
				@include transition(all ease-in-out .2s);

				&:after {
					content: "\e64b";
					font-family: "themify";
					position: absolute;
					right: 0;
					top: 50%;
					margin-top: -20px;
					display: block;
					width: 40px;
					line-height: 40px;
					text-align: center;
					@include transition(all ease-in-out .2s);
				}

				//   
				&.active {
					color: $g-dark-blue;

					&:after {
						@include transform(rotate(180deg));
					}
				}

			}
			
			.hidden-block {
				display: none;
				padding-top: 10px;
				padding-bottom: 10px;
				padding-left: $menu-padding;

				.under-level__link {
					@include color-text($color: $g-light-blue, $size: 14px);
					line-height: 24px;
					padding: 5px 0;
					display: block;
				}
			}
		}	
	}
	//    submenu  
	.under-level__ul--link {
		margin-top: 20px;

		li {
			padding-left: $menu-padding;
			
			a {
				@include color-text($color: $g-light-blue, $size: 14px);
				display: block;
				line-height: 20px;
				padding: 10px 0;
			}
		}
	}
	// ++==++
}

//  
.head-site {
	margin-bottom: 35px;
	.head-site__phone {
		@include span(6 of 12);
		.head-site__phone--num {
			@include color-text($color: $g-dark-blue, $size: 24px);
			line-height: 50px;
		}
	}

	.head-site__usefull-link {
		font-size: 0;
		text-align: right;
		height: 50px;
		@include span(6 of 12);
		
		.head-site__personal {
			display: inline-block;
			vertical-align: top;
			margin-right: 20px;

			 @include link-inline($class-block: head-site__personal__b, $class-link: head-site__personal__link);
			.head-site__personal__b {
				height: 50px;
				line-height: 50px;
				display: inline-block;
				
				.head-site__personal__link {
					@include color-text($color: $g-light-blue, $size: 14px);
				}
			}

		}
	}
}

//   
.head-site__product-link {
	display: inline-block;
	text-align: left;
	line-height: 50px;
	height: 50px;
	
	.head-site__personal-link {
		@include personal-btn();
		display: inline-block;
		margin-left: 10px;
		cursor: pointer;
		@include transition( all ease-in-out .3s .3s );

		.head-site--hidden-full {
			width: 120px;
			overflow: hidden;
		}

		&:first-child {
			margin-left: 0;
		}

		[class^="ti-"], [class*=" ti-"] {
			font-size: 24px;
			line-height: 50px;
			width: 50px;
			text-align: center;
			display: table-cell;
			vertical-align: top;
		}

		&.hover-link:hover {
			width: 120px;
		}

		.head-site__personal-link__ball {
			display: none;
		}
		//     ,      no-empty
		&.no-empty {
			background: $g-light-blue;
			color: #fff;
			position: relative;

			.head-site__personal-link__ball {
				display: block;
				background: $g-yellow;
				position: absolute;
				bottom: 7px;
				left: 25px;
				width: 18px;
				height: 18px;
				line-height: 18px;
				text-align: center;
				color: $g-dark-blue;
				font-size: 10px;
				border-radius: 50%;
			}
		}
	}
	.hidden-text_head-site {
		font-size: 12px;
		display: table-cell;
		vertical-align: middle;
		line-height: 16px;
		width: 70px;
	}
}


//  
.sale {
	$sale-height: 160px;
	$margin-btn-block: 10px;

	.sale__container--separate {
		@include span(4 of 12);
	}
	.sale__container__block {
		height: $sale-height;
		position: relative;
		background: #fff;
		box-shadow: rgba(0,0,0,.2) 0 1px 2px;
		@include transition( all .4s ease-in-out );
		margin-bottom: $margin-btn-block;

		&:last-child {
			margin-bottom: 0;
		}


		.sale__info {
			width: 50%;
			padding-left: 15px;
			height: $sale-height;
			background: $g-yellow;
			z-index: 3;

			.sale__info__head {
				padding-top: 15px;
				padding-right: 15px;
				font-size: 14px;
				color: $g-dark-blue;
				line-height: 18px;
				margin-bottom: 25px;
			}

			.sale__info__price {
				font-size: 0;
				.sale__info__price--currency {
					font-size: 14px;
				}

				.sale__info__price--new {
					font-size: 18px;
					color: #900;
					display: inline-block;

				}
				.sale__info__price--old {
					font-size: 14px;
					display: inline-block;
					padding-left: 5px;
					text-decoration: line-through;
				}
			}
		}
		.sale__img {
			width: 50%;

			.sale__img--cell {
				height: $sale-height;
				text-align: center;
				width: 100%;
				position: relative;
				
				&:before,
				&:after {
					@include pseudo-elem();
					@include transition(all .2s ease-in-out);

				}
				&:after {
					content: "\e611";
					font-family: "themify";
					text-align: center;
					line-height: 48px;
					font-size: 24px;
					color: $g-dark-blue;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					background: $g-yellow;
					width: 50px;
					height: 50px;
					border: 1px solid #fff;
					opacity: 0;
					filter:Alpha(Opacity=0);
				}

				&:hover {
					&:before {
						width: 100%;
						height: 100%;
						top: 0;
						left: 0;
						background: rgba(255,255,255, .7);
					}
					&:after {
						opacity: 1;
						filter:Alpha(Opacity=100);
					}
				}
			}

			.sale__img__link {
				width: 100%;
				height: 100%;
				display: block;
			}
		}
		//  
		.sale__discount {
			$deal-color: #dd2c00;
			position: absolute;
			top: 0;
			width: 40px;
			padding-top: 15px;
			padding-bottom: 0;
			color: #fff;
			text-align: center;
			background: $deal-color;

			&:after {
				content: "";
				position: absolute;
				top: 100%;
				@include calc(top, "100% - 1px");
				left: 0;
				border: solid transparent;
				height: 10px;
				width: 40px;
				border-color: transparent;
				border-left-color: #dd2c00;
				border-top-color: #dd2c00;
				border-left-width: 40px;
				border-top: 0;

			}
		}
		// ++==++
		//       
		&.left-sale {
			.sale__info {
				position: absolute;
				left: 0;
				top: 0;

				&:after {
					content: "";
					position: absolute;
					top: 20px;
					left: 100%;
					@include calc(left, "100% - 1px");
					@include triangle(20px, $g-yellow, right);
				}
			}

			.sale__img {
				float: right;
			}

			.sale__discount {
				right: 10px;
			}
		}
		// ++==++
		//       
		&.right-sale {
			.sale__info {
				position: absolute;
				right: 0;
				top: 0;

				&:after {
					content: "";
					position: absolute;
					top: 20px;
					right: 100%;
					@include calc(right, "100% - 1px");
					@include triangle(20px, $g-yellow, left);
				}
			}

			.sale__img {
				float: left;
			}

			.sale__discount {
				left: 10px;
			}
		}
		// ++==++

		//    
		&.main-sale {
			height: $sale-height*2 + $margin-btn-block;
			
			.sale__img {
				width: 100%;
			}

			.sale__info {
				background: none;
				width: 100%;
				height: auto;
				position: absolute;
				bottom: 20px;
				left: 0;
			}

			.sale__discount {
				left: 20px;
			}
			.sale__info__head {
				color: $g-light-blue;
			}
		}

		// ++==++

		.sale__more-info {
			& > div {
				display: inline-block;
				vertical-align: middle;
				line-height: 28px;
			}
			//     
			.sale__more-info__btn-menu {
				font-size: 17px;
				width: 28px;
				height: 28px;
				line-height: 32px;
				text-align: center;
				@include transition(all .2s ease-in-out);

				&:hover {
					cursor: pointer;
				}
			}
			//  
			.sale__more-info__rate {
				font-size: 14px;
				margin-left: 10px;

				& > span {
					display: inline-block;
					height: 28px;
					vertical-align: middle;
				}

				.sale__more-info--rating-star {
					line-height: 28px;
				}
			}
			
		}


		//    
		&:hover {
			@include transform(scale(1.02));
			box-shadow: rgba(0,0,0,.2) 0 3px 10px;
			
			.sale__more-info__btn-menu {
				background: #ddd;
			}
			
		}

	}
}
// ++==++

//    
.more-info {
	margin-top: 50px;

	.more-info__address {
		@include span(3 of 12);

		.watch-map {
			@include link-decor();		
		}
	}

	.more-info__advantages {
		@include span(8 of 12);

		@include last;
		
		.more-info__advantages__block {
			position: relative;
			padding-left: 85px;
			margin-bottom: 20px;

			h4 {
				color: $g-light-blue;
			}
		}
		.more-info__ico {
			@include position(absolute, 0 null null 0)
		}
	}

}
// ++==++






// 
.footer-site {
	color: #fff;
	font-size: 14px;
	line-height: 18px;
	background: url(../img/footer-bg.jpg);
	background-size: cover;
	padding: 28px 0 28px;

	.footer-block {
		@include span(3 of 12);

		a {
			@include color-text($color: #fff, $size: 14px);
		}
	}

	.info {
		@extend .footer-block;

		.span-block {
			display: block;
			margin-bottom: 5px;

			&.name {
				margin-bottom: 10px;
			}
		}
		.phone {
			margin-top: 20px;
			a {
				font-size: 18px;
			}
		}
	}

	.menu {
		@extend .footer-block;

		a {
			margin-bottom: 10px;
			display: inline-block;
		}
	}

	.useful-link {
		@extend .footer-block;

		a {
			margin-bottom: 10px;
			display: inline-block;
		}
		.useful-link__mail {
			margin-top: 20px;
		}
	}

	.pay-link {
		@extend .footer-block;
		
		& > div {
			margin-bottom: 10px;
		}

		.pay-link__card {
			margin-top: 40px;

			a {
				display: inline-block;
				margin-left: 3px;
				&:first-child {
					margin-left: 0;
				}
			}
		}
	}
}
// ++==++

//   popup
.popup-container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	min-height: 100%;
	@include transition(all ease-in-out .6s);
	@include transform(translateX(-100%));
	z-index: 3;
	background: #fff;
	@include close-btn();

	&.open {
		@include transform(translateX(0%));
	}

	&.all-width {
		padding-left: $menuWidth;
	}

}


// 
.bx-wrapper .bx-viewport {
	height: auto!important;
}
.bxslider1,
.bxslider2 {
	z-index: 1;
}

.product-slider {
	position: relative;
	margin-bottom: 80px;

	.product-slider__diving {
		@include span(11 of 30);
		@include calc(width, "50% + 20px");
		position: relative;
		z-index: 2;
	
		img {
			max-width: 100%;
			width: 100%;
			height: auto;
			display: block;
		}

		.bx-pager {
			top: 40px;
			left: 0;
		}

	}
	.product-slider__earth {
		@include span(11 of 30);
		@include calc(width, "50% + 20px");
		position: absolute;
		z-index: 1;
		top: 30px;
		right: 0;
		
		img {
			max-width: 100%;
			width: 100%;
			height: auto;
			display: block;
		}

		.bx-pager {
			bottom: 40px;
			right: 0;
		}
	}

	.g-hover-slider {
		* {
			@include transition(all ease-in-out .5s);
		}


		.diving__block {
			position: relative;
			box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3);

		
			.bx-wrapper {
				
				&:after {
					@include transition(all ease-in-out .5s);
					content: "";
					position: absolute;
					opacity: 1;
					filter:Alpha(Opacity=100);
					visibility: visible;
					z-index: 2;
					top: 0;
					left: 0;
					display: block;
					height: 100%;
					width: 100%;
					background: rgba(0,0,0,.6);
				}
			}
			//   pager
			.bx-pager {
				position: absolute;
				z-index: 2;

				a {
					@include color-text($color: #fff, $size: 16px);
					display: block;
					padding: 10px 20px;
					line-height: 20px;
					cursor: pointer;
					

				}
			}
		}


		//     
		&.hovered {

			& > div {
				box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.3);
			}

			.diving__block {
			
				a {
					&.active {
						background: $g-light-blue;
					}
				}

			.bx-wrapper {
			
				&:after {
					opacity: 0;
					filter:Alpha(Opacity=0);
					visibility: hidden;
				}
			}
		}
		// ++==++
		
		
		}

	}
}








@media (min-width: 1199px) {
	.wrapper-block {
		width: $big-wrap;
	}
}

@media (max-width: 991px) {
	.wrapper-block{
			width: $tablet-wrap;
	}
}

@media (max-width: 767px) {
	.wrapper-block{
		width: $mobile-wrap;
		min-width: 320px;
	}
}